.welcome {
  position: relative;
  width: 100%;
  height: 100vh;
  min-height: 35rem;
  padding: 15rem 0;
  background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.3) 0%,
      rgba(0, 0, 0, 0.7) 75%,
      #000000 100%
    ),
    url("../img/main_4.jpg");
  background-position: center right;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;
}
.welcome {
  h1 {
    font-family: "Montserrat", sans-serif;
    font-size: 2.5rem;
    font-weight: 600;
    color: #ffffff;
    padding-bottom: 40px;
  }
}

.welcome {
  h4 {
    font-family: "Montserrat", sans-serif;
    font-size: 1.5rem;
    font-weight: 600;
    color: #ffffff;
  }
}

@media (min-width: 768px) {
  .welcome h1 {
    font-size: 4rem;
    line-height: 4rem;
  }
}
@media (min-width: 992px) {
  .welcome {
    height: 100vh;
    padding: 0;
  }
  .welcome h1 {
    font-size: 6.5rem;
    line-height: 6.5rem;
  }
}

.openSource {
  background-color: #000;
  h1 {
    font-family: "Montserrat", sans-serif;
    font-size: 1.5rem;
    font-weight: 600;
    color: #ffffff;
  }
  .container {
    div {
      padding-top: 20px;
      padding-bottom: 40px;
      display: flex;
      .project {
        padding-right: 20px;
        a {
          .title {
            font-family: "Montserrat", sans-serif;
            font-size: 1rem;
            font-weight: 600;
            color: #ffffff;
          }
        }

        &.thismypc {
          a {
            .icon {
              background: url("../img/logo-mini.png") no-repeat center center;
              background-size: contain;
              height: 100px;
              width: 200px;
            }
          }
        }

        &.electron {
          a {
            .icon {
              background: url("../img/electron.svg") no-repeat center center;
              background-size: contain;
              height: 100px;
              width: 200px;
            }
          }
        }
      }
    }

    ul {
      margin: 0;
    }
  }
}

.socialMedia {
  background-color: #000;
  h1 {
    font-family: "Montserrat", sans-serif;
    font-size: 1.5rem;
    font-weight: 600;
    color: #ffffff;
  }
  .container {
    div {
      padding-top: 20px;
      padding-bottom: 40px;
      display: flex;
      .project {
        padding-right: 60px;
        a {
          .title {
            font-family: "Montserrat", sans-serif;
            font-size: 1rem;
            font-weight: 600;
            color: #ffffff;
          }
        }

        &.youtube {
          a {
            .icon {
              background: url("../img/yt_logo_rgb_dark.png") no-repeat center center;
              background-size: contain;
              height: 70px;
              width: 200px;
            }
          }
        }

        &.linkdIn
         {
          a {
            .icon {
              background: url("../img/linkedIn.png") no-repeat center center;
              background-size: contain;
              height: 70px;
              width: 70px;
            }
          }
        }

        &.reddit {
          a {
            .icon {
              background: url("../img/reddit.svg") no-repeat center center;
              background-size: contain;
              height: 70px;
              width: 70px;
            }
          }
        }
      }
    }

    ul {
      margin: 0;
    }
  }
}

.news > div > img {
  height: 100vh;
  width: auto;
}

.tech {
  position: relative;
  width: 100%;
  padding: 1px 0;
  background-color: #000;

  .container {
    h1 {
      font-family: "Montserrat", sans-serif;
      font-size: 1.5rem;
      font-weight: 600;
      color: #ffffff;
    }

    .frameworks {
      color: #fff;
    }

    .mainRow {
      display: flex;
      padding-bottom: 40px;
    }

    .mainRow > div {
      width: 50%;
    }

    .contentContainer {
      padding: 20px;
      max-width: 350px;
      min-width: 150px;
      margin: 0 auto;
      border-radius: 10px;
    }

    .skillBar {
      margin-bottom: 26px;
      margin-bottom: 0.5em;
    }
    .skillBar {
      p {
        font-size: 21px;

        text-transform: none;
        font-family: "Montserrat", sans-serif;

        font-weight: bold;
        margin-bottom: 7px;
        margin-bottom: 0.33em;
        color: #fff;
      }
    }

    .skillBar {
      p {
        span {
          font-size: 16px;

          text-transform: none;
          font-family: "Montserrat", sans-serif;

          font-weight: bold;
          margin-bottom: 7px;
          margin-bottom: 0.33em;
          color: #fff;
        }
      }
    }

    .skillBarContainer {
      width: 100%;
      max-width: 350px;
      height: 26px;
      height: 0.5em;
      background: #e6eae3;
      background: #0866dc;
      background: rgba(8, 102, 220, 0.2);
      overflow: hidden;
      border-radius: 5px;
    }

    .skillBarValue {
      height: 1.66em;
      float: left;
      background: #0866dc;
      background: #0866dc;
      background: rgba(8, 102, 220, 0.75);
    }
  }
}

.news {
  background-color: black;
  padding-top: 20px;
  .container {
    h1 {
      font-family: "Montserrat", sans-serif;
      font-size: 1.5rem;
      font-weight: 600;
      color: #ffffff;
    }
    div {
      padding-top: 20px;
      padding-bottom: 40px;
      width: 70%;
      display: flex;
      align-items: center;
      justify-content: center;
      img {
        width: 100%;
        height: auto;
      }
    }
  }
}

.clip {
  background-color: black;

  .container {
    h1 {
      font-family: "Montserrat", sans-serif;
      font-size: 1.5rem;
      font-weight: 600;
      color: #ffffff;
    }
    div {
      padding-top: 20px;
      padding-bottom: 40px;
      display: flex;
      .video-container {
        overflow: hidden;
        position: relative;
        width: 100%;
      }

      .video-container::after {
        padding-top: 56.25%;
        display: block;
        content: "";
      }

      .video-container iframe {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
      }
    }
  }
}

